/* ==========================================================================
   Standard With Breadcrumbs Title type style - begin
   ========================================================================== */

.edgt-title-holder {
	
	&.edgt-standard-with-breadcrumbs-type {
		
		.edgt-title-info,
		.edgt-breadcrumbs-info {
			display: table-cell;
			vertical-align: middle;
		}

        .edgt-title-inner-holder{
            position: relative;
        }
		
		.edgt-title-info {
			display: inline-block;
		}
		
		.edgt-breadcrumbs-info {
			display: inline-block;
			white-space: nowrap;
            font-size: 14px;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
		}
		
		.edgt-page-title {
			margin: 0;
			color: #212121;
		}
		
		.edgt-page-subtitle {
			margin: 5px 0 0;
		}
		
		.edgt-breadcrumbs {
			color: #888888;
			
			&.edgt-has-inline-style {
				
				a {
					opacity: 1;
					@include edgtTransition(opacity .2s ease-out);
					
					&:hover {
						color: inherit;
						opacity: 0.9;
					}
				}
			}
			
			a,
			span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				color: inherit;
			}
			
			a:hover {
				color: #212121;
			}
		}
	}
}
/* ==========================================================================
   Standard With Breadcrumbs Title type style - end
   ========================================================================== */