/* ==========================================================================
   Title style - begin
   ========================================================================== */

.edgt-title-holder {
    @include edgtRelativeHolderLayout();
    height: 175px;
    background-color: #f7f7f7;
    background-position: center 0;
    background-repeat: no-repeat;
    z-index: 101;
	
	&.edgt-bg-responsive {
		height: auto !important; // because of inline style
		
		.edgt-title-image {
			display: block;
		}
		
		.edgt-title-wrapper {
			position: absolute;
			top: 50%;
			left: 0;
			@include edgtTransform(translateY(-50%));
		}
	}
	
	&.edgt-bg-parallax {
		background-attachment: fixed;
	}
	
	.edgt-title-image {
		display: none;
		
		img {
			display: block;
			width: 100%;
		}
	}
    
    .edgt-title-wrapper {
        @include edgtTableLayout();
    }
    
    .edgt-title-inner {
        @include edgtTableCellLayout();
    }
}
/* ==========================================================================
   Title style - end
   ========================================================================== */