/* ==========================================================================
   Breadcrumbs Title type style - begin
   ========================================================================== */

.edgt-title-holder {
	
	&.edgt-breadcrumbs-type {
		
		.edgt-breadcrumbs {
			@include edgtRelativeHolderLayout();
			color: #fff;
			
			&.edgt-has-inline-style {
				
				a {
					opacity: 1;
					@include edgtTransition(opacity .2s ease-out);
					
					&:hover {
						color: inherit;
						opacity: 0.9;
					}
				}
			}
			
			a,
			span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				color: inherit;
			}
			
			a:hover {
				color: $default-heading-color;
			}
		}
	}
}
/* ==========================================================================
   Breadcrumbs Title type style - end
   ========================================================================== */